<template>
  <div class="tips-template" v-if="show">
    <div class="center-content-template">
      <img class="close" @click="close" src="../assets/close@3x.png"/>

      <img class="center-img" :src="nowImg" />
      <div class="title">{{ tips }}</div>
      <div class="detail">{{ tipsDetail }}</div>

      <div class="btn" @click="action">{{ buttonTips }}</div>
    </div>
  </div>
</template>

<script>
import { callAppRouter } from '../js/tool.js'

export default {
  name: 'showTips',
  data () {
    return {
      show: false,
      tips: '兑换失败',
      buttonTips: '马上兑换',
      tipsDetail: '当前输入的兑换码为空，请确认兑换码',
      nowImg: '',
      successImg: require('../assets/success.png'),
      failImg: require('../assets/box_no@3x.png')
    }
  },
  methods: {
    action () {
      if (this.tips === '兑换成功') {
        callAppRouter('Redirect', {
          code: 3011
        })
      }
      this.close()
    },
    close () {
      this.show = false
    }
  }
}
</script>

