<template>
  <div class="home animated fadeIn">
    <TipsTemplate ref="tipsTemplate"/>
    <div class="top-template">
      <!-- <img class="back" src="../assets/back_bttn@3x.png"/> -->
      <img class="bg-img" src="../assets/favourable_top_img@3x.png"/>
      <img class="shape" src="../assets/CombinedShape@3x.png" />
    </div>

    <div class="desc">输入优惠码，领取大礼包，为健康加分！</div>

    <input v-model="code" class="key-input" />

    <div class="enter" @click="submitCode">马上兑换</div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import MyInput from '../components/Input.vue'
import TipsTemplate from '../components/showTips.vue'
import { callAppRouter } from '../js/tool.js'

export default {
  name: 'home',
  data () {
    return {
      is_login: false,
      code: '',
      openid: '',
      itVtoggle: ''
    }
  },
  components: {
    TipsTemplate
  },
  methods: {
    getLoginData () {
      if (!this.is_login) {
        return
      }
      callAppRouter('Login', {}, (res, ed) => {
        let parseEd = ''
        try {
          parseEd = JSON.parse(ed)
        } catch {
          parseEd = ed
        }
        this.openid = parseEd.data.openid
        console.log(this.openid)
        if (this.openid && this.itVtoggle) {
          clearInterval(this.itVtoggle)
        }
      })
    },
    callLogin () {
      callAppRouter('isLogin', {}, (res, ed) => {
        const status = JSON.parse(ed).status
        // eslint-disable-next-line
        status === '1' || status === 1 ? this.is_login = true : ''
        this.getLoginData()
      })
    },
    submitCode () {
      /* const sender = {
        url: 'heartide/v3/web/coupon/source/active',
        methods: 'post',
        data: {
          code: this.code || 'asd'
        },
        headers: {
          htopenid: this.openid || 'c357c67dc90d0c733b8c7fd70806eff4'
        }
      } */
      this.$axios.post('heartide/v3/web/coupon/source/active', { code: this.code }, { headers: { htopenid: this.openid || '', 'Access-Control-Allow-Origin': 'true' } }).then((res) => {
        if (res.data.data === '请先登录' || res.data.msg === '请先登录') {
          callAppRouter('Redirect', { code: '1024' }, (res, ed) => {
            this.itVtoggle = setInterval(this.callLogin, 1000)
          })
        } else {
          this.$refs.tipsTemplate.show = true
        }

        const status = parseInt(res.data.status)
        if (status === 1) {
          this.$refs.tipsTemplate.tips = '兑换成功'
          this.$refs.tipsTemplate.nowImg = this.$refs.tipsTemplate.successImg
          this.$refs.tipsTemplate.buttonTips = '马上查看'
        }
        this.$refs.tipsTemplate.nowImg = this.$refs.tipsTemplate.failImg
        this.$refs.tipsTemplate.tips = '兑换失败'
        this.$refs.tipsTemplate.tipsDetail = res.data.data || res.data.msg
        this.$refs.tipsTemplate.buttonTips = '重新输入'
      })
    }
  },
  mounted () {
    this.callLogin()
  }
}
</script>

